.header {
  background-color: #6495ed;
  justify-content: center;
  padding: 10px 0;
  display: flex;
  box-shadow: 0 3px 3px #cdcdcd;
}

.search-form input {
  width: 350px;
  height: 45px;
  border-color: #dfdfdf;
  border-radius: 7px;
  outline: none;
  margin-right: 0;
}

.search-form button {
  width: 65px;
  height: 45px;
  cursor: pointer;
  background-color: #dedede;
  border-color: #dfdfdf;
  border-radius: 5px;
}

.search-form button:hover {
  background-color: #a9a9a9;
  border-color: #a9a9a9;
}

.gallery {
  flex-wrap: wrap;
  gap: 50px;
  margin: 20px;
  display: flex;
}

.photo-card {
  height: 500px;
}

.photo-card img {
  width: 400px;
}

.load-more {
  width: 100px;
  height: 50px;
  color: #fff;
  cursor: pointer;
  background-color: #a9a9a9;
  border: none;
  border-radius: 5px;
  outline: none;
  margin: 0 auto;
  display: block;
}

.load-more:hover {
  background-color: #696969;
  border-color: #696969;
}

.is-hidden {
  display: none;
}

